.app {
    height: 100vh;

    .sections {
        width: 100%;
        height: calc(100vh - 70px); //0px 70px
        background-color: lightsalmon;
        position: relative;
        top: 70px; //comment out
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;

        /*
        scrollbar-width: none; //for firefox
        &::-webkit-scrollbar {
            display: none;
        }*/

        > * {
            width: 100vw;
            height: calc(100vh - 70px); //0px 70px
            scroll-snap-align: start;
        }
    }
}